import React from "react";
import { Link, NavLink } from "react-router-dom"; //  NavLink  Link
import logo from "../../../assets/images/logo.svg";
import "./navbar.scss";

export const Navbar = () => {
  return (
    <nav className="navbar">
      <div className="navbar-logo">
        <Link to="/">
          <img src={logo} alt="" width="45" height="45" />
        </Link>
        <Link to="/" style={{textDecoration: 'none'}}>
          <span className="navbar-logo-text" > React</span>
        </Link>
      </div>
      <ul className="navbar-links">
        <li>
          <NavLink className="navbar-link" to="/" exact activeClassName="active">
            
          </NavLink>
        </li>
        <li>
          <NavLink className="navbar-link" to="/team" activeClassName="active">
            
          </NavLink>
        </li>
        <li>
          <NavLink className="navbar-link" to="/favourites" activeClassName="active">
            
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};
