import React from "react";
import { Icon } from "../../../../../../shared/ui";
import {
    faBriefcase,
    faCode,
    faDraftingCompass,
    faShieldAlt,
    faMagic,
} from "@fortawesome/free-solid-svg-icons";
import "./infoCard.scss";

//   
const cardsData = [
    {
        id: 1,
        icon: faBriefcase,
        title: "",
        description:
            " 10      .        .",
    },
    {
        id: 2,
        icon: faCode,
        title: "",
        description:
            "  ,   React, Node.js  NestJS,     .",
    },
    {
        id: 3,
        icon: faDraftingCompass,
        title: "",
        description:
            " ,         ,      .",
    },
    {
        id: 4,
        icon: faShieldAlt,
        title: "",
        description:
            "    ,           .",
    },
    {
        id: 5,
        icon: faMagic,
        title: "",
        description:
            "            .           ,      .",
    },
];

export const InfoCard = () => {
    return (
        <div className="card-container">
            {cardsData.map((card) => (
                <div className="card" key={card.id}>
                    <Icon className="card-icon" icon={card.icon} margin="0 0 0 10px" size={40}/>
                    <h3>{card.title}</h3>
                    <p>{card.description}</p>
                </div>
            ))}
        </div>
    );
};
